<template>
  <div>
    <el-popover placement="bottom" title="ajouter session" width="200" trigger="click">
      <el-input placeholder="start" v-model="start" ></el-input>
      <el-input placeholder="end" v-model="end" ></el-input>
      <el-input placeholder="cashout" v-model="cashout" ></el-input>
      <el-input placeholder="date" v-model="date" ></el-input>
      <el-input placeholder="comment" v-model="comment" @blur="createSession(start, end, date, cashout, comment)"></el-input>
      <el-button round slot="reference" type="success">Ajouter une session</el-button> 
    </el-popover>
    <el-table :data="sessionsData.filter((data) => !search || data.start.includes(search))" style="width:100%">
      <el-table-column label="Début" prop="start"></el-table-column>
      <el-table-column label="Fin" prop="end"></el-table-column>
      <el-table-column label="Cashout" prop="cashout"></el-table-column>
      <el-table-column label="Date" prop="date"></el-table-column>
      <el-table-column label="Comment" prop="comment"></el-table-column>
      <el-table-column align="right">
        <template slot-scope="scope"> 
          <el-popover placement="bottom" title="Edit session" width="200" trigger="click">
            <el-input placeholder="5000" v-model="scope.row.start" @blur="updateSession(scope.row.id, scope.row.start, date)"></el-input>
          </el-popover>
          <el-button size="mini" type="danger" @click="deleteSession(scope.row.id)">Supprimer</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import firebase from './firebaseConfig'
const db = firebase.firestore()
export default {
  data(){
    return {
      start: '',
      end: '',
      date: new Date().toISOString().slice(0,10),
      cashout: '',
      comment: '',
      sessionsData: [],
      search: '',
    };
  },
  methods: {
    createSession(start, end, date, cashout, comment){
      if(start != "", end !="", cashout !=""){
        db.collection("sessions")
        .add({ start: start, end: end, date:date, cashout:cashout, comment:comment  })
        .then(()=> {
          console.log("Bien ajouté")
          this.readSession()
        })
        .catch((err)=> {
          console.log("Pas ajouté ", err)
        })
        this.start = "",
        this.end = "",
        this.date = "", 
        this.cashout = "",
        this.comment = ""
      }
    },
    readSession(){
      this.sessionsData = []
      db.collection('sessions')
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          this.sessionsData.push({
            id: doc.id,
            start: doc.data().start, 
            end: doc.data().end, 
            date: doc.data().date, 
            cashout:doc.data().cashout,
            comment: doc.data().comment,
          })
          console.log(doc.id, " => ", doc.data())
        })
      })
      .catch((err) => {
        console.log('impossible de récup les data ', err)
      })
    },
    updateSession(id, start, end, date, cashout, comment){
      db.collection('sessions')
      .doc(id)
      .update({
        start: start,
        end: end,
        date: date,
        cashout: cashout,
        comment: comment
      })
      .then(() => {
        console.log('Session bien modifiée')
        this.readSession()
      })
      .catch((err) => {
        console.log('Oops ', err)
      })
    },
    deleteSession(id) {
      db.collection('sessions')
      .doc(id)
      .delete()
      .then(() => {
        console.log('Session supprimée')
        this.readSession()
      })
      .catch((err) => {
        console.log('Oops delete: ', err)
      })
    },
  },
  mounted(){
    this.readSession()
  }
}
</script>